import React, { Component } from "react";
import PageHelmet from "../component/common/Helmet";
import ModalVideo from "react-modal-video";
import {
  FaTwitter,
  FaInstagram,
  FaFacebookF,
  FaLinkedinIn,
} from "react-icons/fa";
import ScrollToTop from "react-scroll-up";
import { FiChevronUp } from "react-icons/fi";
import Header from "../component/header/Header";
import Footer from "../component/footer/Footer";

const SocialShare = [
  {
    Social: <FaFacebookF />,
    link: "https://www.facebook.com/carlo.lombardi.gomez/",
  },
  {
    Social: <FaLinkedinIn />,
    link: "https://www.linkedin.com/in/carlo-lombardi-5574121ba/",
  },
  {
    Social: <FaInstagram />,
    link: "https://www.instagram.com/carlolombardi_/",
  },
  { Social: <FaTwitter />, link: "https://twitter.com/CarlowLombardi" },
];

class PortfolioDetails extends Component {
  constructor() {
    super();
    this.state = {
      isOpen: false,
    };
    this.openModal = this.openModal.bind(this);
  }
  openModal() {
    this.setState({ isOpen: true });
  }
  render() {
    return (
      <React.Fragment>
        <PageHelmet pageTitle="Portfolio Details" />

        <Header
          headertransparent="header--transparent"
          colorblack="color--black"
          logoname="logo.png"
        />

        {/* Start Breadcrump Area */}
        <div
          className="rn-page-title-area pt--120 pb--190 bg_image bg_image--4"
          data-black-overlay="7"
        >
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <div className="rn-page-title text-center pt--100">
                  <h2 className="title theme-gradient">El Milagro</h2>
                  <p>
                    A project inspired by the two things I like the most about
                    cooking and technology
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* End Breadcrump Area */}

        {/* Start Portfolio Details */}
        <div className="rn-portfolio-details ptb--120 bg_color--1">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <div className="portfolio-details">
                  <div className="inner">
                    <h2>El Milagro</h2>
                    <p className="subtitle">
                      El Milagro was inspired by two things that I really like,
                      cooking and technology. This was my final graduation
                      project where I used a lot of technologies.
                    </p>
                    <p>
                      This project was inspired by one of the best taquerias
                      here in Dublin where I live. The experience of developing
                      it was enriching. There were hard times of frustration
                      with the typical code not compiling. Analyze how to
                      develop the database as if everything were real, I would
                      still like to add many functionalities in the future. But
                      now I am very proud of what I achieved, I hope you enjoy
                      this video that was presented at my school along with my
                      graduation.
                    </p>

                    {/* <div className="portfolio-view-list d-flex flex-wrap">
                      <div className="port-view">
                        <span>Branch</span>
                        <h4>Ability</h4>
                      </div>

                      <div className="port-view">
                        <span>Project Types</span>
                        <h4>Website</h4>
                      </div>

                      <div className="port-view">
                        <span>Program</span>
                        <h4>View Project</h4>
                      </div>
                    </div> */}

                    <div className="portfolio-share-link mt--20 pb--70 pb_sm--40">
                      <ul className="social-share rn-lg-size d-flex justify-content-start liststyle mt--15">
                        {SocialShare.map((val, i) => (
                          <li key={i}>
                            <a href={`${val.link}`}>{val.Social}</a>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                  <div className="portfolio-thumb-inner">
                    <div className="thumb position-relative mb--30">
                      <img
                        // https://youtu.be/s7D_xK0hIoI
                        src="/assets/images/portfolio/carlo_presentacion.png"
                        alt="Portfolio Images"
                      />
                      <ModalVideo
                        channel="youtube"
                        isOpen={this.state.isOpen}
                        videoId="s7D_xK0hIoI"
                        onClose={() => this.setState({ isOpen: false })}
                      />
                      <button
                        className="video-popup position-top-center"
                        onClick={this.openModal}
                      >
                        <span className="play-icon"></span>
                      </button>
                    </div>
                    {/* 
                    <div className="thumb mb--30">
                      <img
                        src="/assets/images/portfolio/portfolio-big-02.jpg"
                        alt="Portfolio Images"
                      />
                    </div>

                    <div className="thumb">
                      <img
                        src="/assets/images/portfolio/portfolio-big-01.jpg"
                        alt="Portfolio Images"
                      />
                    </div> */}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* End Portfolio Details */}

        {/* Start Related Work */}
        <div className="portfolio-related-work pb--120 bg_color--1">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <div className="section-title text-center">
                  <h2>Technologies used</h2>
                </div>
              </div>
            </div>
            <div className="row mt--10">
              {/* Start Single Portfolio */}
              <div className="col-lg-6 col-md-6 col-12">
                <div className="related-work text-center mt--30">
                  <div className="thumb"></div>
                  <div className="inner">
                    <h4>
                      <a href="https://reactjs.org/">React JS</a>
                    </h4>
                    <h4>
                      I declare myself passionate about React js, it is one of
                      the best technologies that I have been able to learn, I
                      used this technology in this project and I was surprised
                      by everything that can be done, it is so intuitive and
                      with so much potential. Of course, it is not easy to learn
                      it, it has a very steep learning curve, I still learn a
                      lot from it but the more I understand it, the more I like
                      it. You can use it to develop simple applications as well
                      as you can use it to develop giant applications. There is
                      still a long way to go due to its complexity, but I
                      definitely recommend it 100%.
                    </h4>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 col-md-6 col-12">
                <div className="related-work text-center mt--30">
                  <div className="thumb">
                    <a href="https://reactjs.org/">
                      <img
                        src="/assets/images/portfolio/related-image-02.jpg"
                        alt="React Js"
                      />
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="row mt--10">
              {/* Start Single Portfolio */}
              <div className="col-lg-6 col-md-6 col-12">
                <div className="related-work text-center mt--30">
                  <div className="thumb">
                    <a href="https://getbootstrap.com/">
                      <img
                        src="/assets/images/portfolio/Bootstrap_logo.svg.png"
                        alt="React Js"
                      />
                    </a>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 col-md-6 col-12">
                <div className="related-work text-center mt--30">
                  <div className="thumb"></div>
                  <div className="inner">
                    <h4>
                      <a href="https://getbootstrap.com/">Bootstrap</a>
                    </h4>
                    <h4>
                      I learned this technology to streamline the development
                      process, very apart that I really like how easy it can be
                      used and the flexibility that I have to be able to make my
                      pages responsive. I also had the opportunity to use the
                      React Bootstrap library which is mostly similar to
                      Bootstrap and I was delighted.
                    </h4>
                  </div>
                </div>
              </div>
            </div>
            <div className="row mt--10">
              {/* Start Single Portfolio */}
              <div className="col-lg-6 col-md-6 col-12">
                <div className="related-work text-center mt--30">
                  <div className="thumb"></div>
                  <div className="inner">
                    <h4>
                      <a href="https://www.mongodb.com/">Mongo DB</a>
                    </h4>
                    <h4>
                      Mongodb is here to stay is a very intuitive, horizontal
                      and scalable technology. You have to know how to use it as
                      and at what times, I feel that little by little they
                      improve it and present themselves for more stricter
                      projects and you know how to use it well and understand
                      it, your life will be an odyssey. I used it for my project
                      and I can't say anything but a little difficult at first
                      but the results are the best if you keep using it.
                    </h4>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 col-md-6 col-12">
                <div className="related-work text-center mt--30">
                  <div className="thumb">
                    <a href="https://www.mongodb.com/">
                      <img
                        src="/assets/images/portfolio/MongoDB-Emblem.jpg"
                        alt="React Js"
                      />
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="row mt--10">
              {/* Start Single Portfolio */}
              <div className="col-lg-6 col-md-6 col-12">
                <div className="related-work text-center mt--30">
                  <div className="thumb">
                    <a href="https://nodejs.org/en/">
                      <img
                        src="/assets/images/portfolio/1280px-Node.js_logo.svg.png"
                        alt="React Js"
                      />
                    </a>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 col-md-6 col-12">
                <div className="related-work text-center mt--30">
                  <div className="thumb"></div>
                  <div className="inner">
                    <h4>
                      <a href="https://nodejs.org/en/">Node JS</a>
                    </h4>
                    <h4>
                      I love this technology, I had already learned JavaScript,
                      I had been practicing a lot with this programming language
                      and knowing that with Node JS I could use the same
                      programming language. To be able to handle a series of
                      events much faster, make HTTP requests and much more, I
                      was delighted.
                    </h4>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* End Related Work */}

        {/* Start Back To Top */}
        <div className="backto-top">
          <ScrollToTop showUnder={160}>
            <FiChevronUp />
          </ScrollToTop>
        </div>
        {/* End Back To Top */}

        <Footer />
      </React.Fragment>
    );
  }
}
export default PortfolioDetails;
